<template>
  <div>

    <label :for="the_name">
      <slot></slot>
    </label>
    <input 
      type="text" 
      class="form-control" 
      :id="the_name" 
      :placeholder="the_placeholder" 
      v-model="value" 
      @change="validate"
    >
    <small :id="the_name + 'Tip'" class="form-text text-muted">{{ tip_value }}</small>

  </div>
</template>

<script>
export default {
  name: 'LabelInput',
  props: {
    the_placeholder: {
      type: String
    },
    the_name: {
      type: String
    },
    the_validate_types: {
      type: Array,
      default() { return [ 'empty' ] }
    },
    the_min_length: {
      type: Number,
      default: 3
    },
    the_max_length: {
      type: Number,
      default: 60
    }
  },
  data() {
    return {
      value: '',
      tip_value: ''
    }
  },
  methods: {
    validate() {
      let tip = ''
      for (const code of this.the_validate_types) {
        console.log(code)
        if (code == 'empty') {

        }
      }
    }
  }
}
</script>

<style lang="sass" scoped>
@import "@/common/sass/base.sass"

</style>


