<template>
  <div class="content-wrapper">
    <div class="row mt-1">
      <div class="col-sm-12">
        <h2 class="fw-b text-main">
          <slot name="page_title"></slot>
        </h2>
      </div>
    </div>

    <div class="row mt-2 pt-1">
      <div class="col-sm-12 data-panel-wrapper">
        <div class="data-panel-wrapper def-shadow br-horizonS">

          <member-pa-u-form ref="memberOption" v-if="this.membery != null" :the_member="membery" :the_option="option"></member-pa-u-form>

          <div class="mt-3 float-right">
            <button class="btn btn-info btn-lg" @click="submitMember">
              保存
            </button>
          </div>
        </div>
      </div>
    </div>

    <custom-modal @done_Father="done" :need_cancel="false">
      <div slot="title">提示</div>
      <div slot="content">{{ msg }}</div>
      <div slot="submit_text">跳转至“展示”</div>
    </custom-modal>
  </div>
</template>

<script>
import view from '@/common/es/view'
import config from '@/common/config'
import tool from '@/common/es/tool'

import conn from '@/service/conn'

import MemberPaUForm from './MemberPaUForm.vue'
import CustomModal from '@/components/Modal/CustomModal.vue'

export default {
  name: 'MemberPlus',
  components: { MemberPaUForm, CustomModal },
  props: {
    option: {
      type: String
    },
  },
  data() {
    return {
      memberId: null,
      membery: null,
      msg: '未检测到需要修改的用户！'
    }
  },
  mounted() {
    if (this.option == 'plus') {
      this.judgePlus()
    } else if (this.option == 'update') {
      this.judgeUpdate()
    }
  },
  methods: {
    judgePlus() {
      this.membery = {}
    },
    judgeUpdate() {

      this.memberId = this.$route.query.memberId

      if (this.memberId != null) {
        conn.member.getOne(this.memberId).then(res => {
          this.membery = res
        })
      } else {
        tool.showModal('customModal')
      }

    },
    submitMember() {
      this.$refs.memberOption.submitMemberForm()
    },
    done() {
      tool.closeModal('customModal')

      setTimeout(() => {
        this.$router.push('/home/member')
      }, 700)
    }
  }
}
</script>

<style lang="sass" scoped>

</style>


